import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import { toast } from 'react-toastify';
import commonService from '../../../core/services/commonService';
//import AutoCompletePlaces from '../../../core/google-map/AutoCompletePlaces';
import Loader from '../../Loader/Loader';
import DoctorsData from './DoctorsData';
import './Doctors.css'

class Doctors extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,      
      doctorList: [],
      loading: true,
      rowIndex: -1,
      formProccessing: false,
      superSpecializationList: [],
      specializationList: [],
      formField: {doctor_name: '', email: '', firstName: '', lastName: '', skype_id: '', latitude: '', longitude: '',  phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '', status: '' },
      errors: {doctor_name: '', email: '', contact_person: '', role: '', address: '', error: ''},
      selectedSpecialization: [],
      selectedSuperSpecialization: [],
      degreeList: [],
      formValid: true,
      filterItem: { filter_doctor_id: '', country: '', state: '', custom_search: '', specialization: ""},

    } 
    this.handleEditDoctor = this.handleEditDoctor.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteDoctor = this.handleDeleteDoctor.bind(this);
    this.filterDoctorsList = this.filterDoctorsList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
    this.setLatitudeLongitude = this.setLatitudeLongitude.bind(this);
    
  }
  // Fetch the doctor List
  componentDidMount() { 
    this.doctorList();
    this.getSpecialization();
    this.getSuperSpecialization();
    this.getDegreeList();
  }

  

  setLatitudeLongitude(address, latLng, city = "", state = "", country = "", postal_code = ""){
    let formField = this.state.formField;
    formField.state = state;
    formField.latitude = latLng.latitude;
    formField.longitude = latLng.longitude;
    formField.country = country;formField.city = city;formField.postalCode = postal_code;
    formField.address = address
    this.setState({ formField: formField });
  }
  /*doctor List API*/
  doctorList(filterItem = {}) {
    let doctorQuery = "";
    
    if(filterItem.country !== undefined && filterItem.country !== "" ) 
      doctorQuery += (doctorQuery !=="" ) ? "&country="+filterItem.country: "?country="+filterItem.country;
    if(filterItem.state !== undefined && filterItem.state !== "" ) 
      doctorQuery += (doctorQuery !=="" ) ? "&state="+filterItem.state: "?state="+filterItem.state;
    if(filterItem.custom_search !== undefined && filterItem.custom_search !== "" ) 
      doctorQuery += (doctorQuery !=="" ) ? "&keyword="+filterItem.custom_search: "?keyword="+filterItem.custom_search;
    if(filterItem.specialization !== undefined && filterItem.specialization !== "" ) 
      doctorQuery += (doctorQuery !=="" ) ? "&specialization="+filterItem.specialization: "?specialization="+filterItem.specialization;
    if(filterItem.status !== undefined && filterItem.status !== "" ) {
      let statusTxt = 1;
      statusTxt = commonService.getStatusVal(filterItem.status);
      doctorQuery += (doctorQuery !=="" ) ? "&status="+filterItem.status: "?status="+statusTxt;
    }
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('doctors'+doctorQuery)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            return;
          }   

          this.setState({loading:false, doctorList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);
          }
        } )
    } )
  }

  getDegreeList(){

    commonService.getAPIWithAccessToken('degree')
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {            
          toast.error(res.data.message);    
          return;
        }   

        this.setState({degreeList: res.data.data});     
       
      } )
      .catch( err => {   
              
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else {
          
          toast.error(err.message);    
        }
      } )
  }

  getSpecialization() {
    let filterParmas = '?isSuperSpecialization='+false;
    commonService.getAPIWithAccessToken('specialization'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {            
            toast.error(res.data.message);    
            return;
          }   

          this.setState({specializationList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            
            toast.error(err.message);    
          }
        } )
  }

  getSuperSpecialization(){
    let filterParmas = '?isSuperSpecialization='+true;
    commonService.getAPIWithAccessToken('specialization'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {            
            toast.error(res.data.message);    
            return;
          }   

          this.setState({superSpecializationList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            
            toast.error(err.message);    
          }
        } )
  }
  /* Submit Form Handler*/
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.validateForm()){
      return false;
    }
    this.setState( { formProccessing: true}, () => {
      const formInputField = this.state.formField;
      const formData = {
        "email": formInputField.email,
        "firstName": formInputField.firstName,
        "lastName": formInputField.lastName, 
        "phoneNumber": formInputField.phoneNumber, 
        "address": formInputField.address, 
        "roleName": formInputField.role, 
        "city": formInputField.city, 
        "state": formInputField.state, 
        "latitude": formInputField.latitude,
        "longitude": formInputField.longitude, 
        "country": formInputField.country, 
        "postalCode": formInputField.postalCode, 
        "doctorName": formInputField.doctor_name,
        "skype_id": formInputField.skype_id,
        "specialization": this.state.selectedSpecialization.map(item => item.specializationId),
        "superSpecialization": this.state.selectedSuperSpecialization.map(item => item.specializationId),
        "title": formInputField.prefix,
        "degreeId": formInputField.degreeId,
        "status": commonService.getStatusVal(formInputField.status)
      };
      const rowIndex = this.state.rowIndex;
      if(rowIndex > -1) {
        const doctorInfo = this.state.doctorList[rowIndex];
        commonService.putAPIWithAccessToken('doctors/'+doctorInfo.organizationId, formData)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
           
            this.setState( { formProccessing: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.doctorList();
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
      else{
        commonService.postAPIWithAccessToken('doctors', formData)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) { 
            this.setState( { formProccessing: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false, formProccessing: false});
          toast.success(res.data.message);
          this.doctorList();
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { formProccessing: false } );
            toast.error(err.message);
        } )
      }
    } );
    
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };
  
  
  /* Validate Form */
  validateForm() {
    let formValid = false;
    let formField = this.state.formField; 
    let errors = {};
    var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
    if(formField.firstName === undefined || formField.firstName.trim() === ""){
      formValid = true;
      errors['firstName'] = "First name field required!";
    }
    if(formField.email === undefined || formField.email.trim() === ""){
      formValid = true;
      errors['email'] = "Email field required!";
    }
    else if(!(pattern.test(formField.email))){
      formValid = true;
      errors['email'] = "Invalid email format";
    }
    if(this.state.selectedSuperSpecialization.length === 0){
      formValid = true;
      errors['superSpecialization'] = "Please select super specialization";
    }
    if(this.state.selectedSpecialization.length === 0){
      formValid = true;
      errors['specialization'] = "Please select specialization";
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      rowIndex: -1,
      formValid: false,
      formProccessing : false,
      selectedSpecialization: [],
      selectedSuperSpecialization: [],
      formField: {doctor_name: '', prefix: '', degree: '', email: '', firstName: '', lastName: '', phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '' },
      formErrors: {doctor_name: '', email: '', contact_person: '', role: '', address: '', error: ''}
    });
  }
  /* Edit doctor*/
  handleEditDoctor(rowIndex){
      const doctorInfo = this.state.doctorList[rowIndex];
     
      let status = commonService.getUserStatus(doctorInfo.status);
      const formField = {
        doctor_name: doctorInfo.doctorName, 
        email: doctorInfo.email, 
        firstName: doctorInfo.firstName, 
        lastName: doctorInfo.lastName, 
        phoneNumber: doctorInfo.phoneNumber, 
        address: doctorInfo.address, 
        city: doctorInfo.city, 
        state: doctorInfo.state, 
        country: doctorInfo.country, 
        postalCode: doctorInfo.postalCode, 
        role: doctorInfo.roleName,
        skype_id: doctorInfo.skype_id || "",
        prefix: doctorInfo.title || "",
        degreeId: doctorInfo.degreeId || "",
        selectedSpecialization: doctorInfo.doctorsspecialization || [],
        selectedSuperSpecialization: doctorInfo.doctorssuperSpecialization || [],
        status: status };
      this.setState({rowIndex: rowIndex, selectedSpecialization: [], selectedSuperSpecialization: [], formField: formField, modal: true, formValid: true, formProccessing: false});
  }
  /* Delete doctor*/
  handleDeleteDoctor(rowIndex){
    const doctorInfo = this.state.doctorList[rowIndex];
    this.setState( { loading : true}, () => {   
      commonService.deleteAPIWithAccessToken('doctors', {organizationId: doctorInfo.organizationId})
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
         
          this.setState( { loading : false} );
          toast.error(res.data.message);
          return;
        } 
        
        this.setState({ modal: false, loading : false});
        toast.success(res.data.message);
        this.doctorList();
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else
          this.setState( { loading : false } );
          toast.error(err.message);
      } )
    } );
   
    
  }

  changeSpecializationHandler = event => {
    const name = event.target.name;
    const value = event.target.value;

    if(name === "superSpecializationId"){
      let selectedSuperSpecialization = this.state.selectedSuperSpecialization;
      let checkAlreadySelected = selectedSuperSpecialization.filter(function(item) { return item.specializationId === value;});
      if(checkAlreadySelected.length === 0){
        let getSpecializationInfo = this.state.superSpecializationList.filter(function(item) { return item.specializationId === value;});
        if(getSpecializationInfo.length > 0 ){
          selectedSuperSpecialization.push(getSpecializationInfo[0]);
          this.setState({selectedSuperSpecialization: selectedSuperSpecialization})
        }
      }
    }
    else{
      let selectedSpecialization = this.state.selectedSpecialization;
      let checkAlreadySelected = selectedSpecialization.filter(function(item) { return item.specializationId === value;});
      if(checkAlreadySelected.length === 0){
        let getSpecializationInfo = this.state.specializationList.filter(function(item) { return item.specializationId === value;});
        if(getSpecializationInfo.length > 0 ){
          selectedSpecialization.push(getSpecializationInfo[0]);
          this.setState({selectedSpecialization: selectedSpecialization})
        }
      }
    }
  }
  filterDoctorsList(){
    const filterItem = this.state.filterItem;
    this.doctorList(filterItem);
  }
  
  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  resetSearchFilter() {
    this.setState({filterItem: { filter_doctor_id: '', country: '', status:'', state: '', custom_search: '', specialization: ''}});
    this.doctorList();
  }

  removeSelectedSpecialization(index, type='specialization', event){
    event.preventDefault();
    if(type === "specialization"){
      let selectedSpecialization = this.state.selectedSpecialization;
      selectedSpecialization.splice(index, 1);
      this.setState({selectedSpecialization: selectedSpecialization});
    }
    else{
      let selectedSuperSpecialization = this.state.selectedSuperSpecialization;
      selectedSuperSpecialization.splice(index, 1);
      this.setState({selectedSuperSpecialization: selectedSuperSpecialization});
    }
  }
  render() {

    const { doctorList, loading, modal, formProccessing, errors } = this.state;     
    let loaderElement = '';
    if(loading)        
      loaderElement = <Loader />
    const processingBtnText = <>Submit <i className="fa fa-spinner"></i></>;
    return (
      <div className="animated fadeIn">
        <Row>
          
          {loaderElement}
          <Col lg={12}>
            <Card className="oep-card">
              <CardHeader className="mainHeading">
                <strong>Investigator List</strong> 
                <Button color="" className="categoryAdd" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add Investigator</Button>
              </CardHeader>
                
                <Row>
                  <Col md={12}>
                    <div className="search-filter">
                    <Row>                      
                      
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Email/ Name</Label>            
                          <Input type="text" placeholder="Search By Email/ Name" id="custom_search" name="custom_search" value={this.state.formField.custom_search} onChange={this.changeFilterHandler} />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Specialization</Label>            
                          <Input type="text" placeholder="Search By Specialization" id="specialization" name="specialization" value={this.state.formField.specialization} onChange={this.changeFilterHandler} />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                          <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                              <option value = "">Select</option>
                              <option value="Active">Active</option>
                              <option value="Inactive">Inactive</option>
                              <option value="Pending">Pending</option>
                              <option value="Block">Block</option>
                          </Input>
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup className="filter-button-section"> 
                          <Button className="search-btn" type="button" onClick={this.filterDoctorsList}>Search</Button> 
                          <Button className="search-btn" id="resetButton" type="button" onClick={this.resetSearchFilter}>Reset</Button> 
                        </FormGroup>             
                      </Col>
                    </Row>
                     </div>  
                  </Col>

                  <Col lg={12}>
                    <DoctorsData data={doctorList} editDoctorAction={this.handleEditDoctor} deleteDoctorAction={this.handleDeleteDoctor} dataTableLoadingStatus = {this.state.loading} />
                  </Col>  
                </Row>
              
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={this.toggle} className="full-width-modal-section organization-modal">
          <ModalHeader toggle={this.toggle}>Investigator</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              <Row>
                <Col md={"2"}>  
                  <FormGroup> 
                    <Label htmlFor="prefix">Prefix</Label>            
                    <Input type="text" placeholder="Prefix " invalid={errors['prefix'] !== undefined && errors['prefix'] !== ""} id="prefix" name="prefix" value={this.state.formField.prefix} onChange={this.changeHandler}  />
                    <div className="invalid-feedback">
                      {errors['prefix']}
                    </div>
                  </FormGroup>
                </Col>
                <Col md={"5"}>  
                  <FormGroup> 
                    <Label htmlFor="firstName">First Name</Label>            
                    <Input type="text" placeholder="First Name *" invalid={errors['firstName'] !== undefined && errors['firstName'] !== ""} id="firstName" name="firstName" value={this.state.formField.firstName} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['firstName']}
                    </div>
                  </FormGroup>
                </Col>
                <Col md={"5"}>  
                  <FormGroup> 
                    <Label htmlFor="lastName">Last Name</Label>            
                    <Input type="text" placeholder="Last Name" id="lastName" name="lastName" value={this.state.formField.lastName} onChange={this.changeHandler}  />
                    <div className="invalid-feedback">
                      {errors['lastName']}
                    </div>
                  </FormGroup>
                </Col> 
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="email">Email</Label>            
                    <Input type="text" placeholder="Email *" invalid={errors['email'] !== undefined && errors['email'] !== ""} id="email" name="email" value={this.state.formField.email} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['email']}
                    </div>
                  </FormGroup>
                </Col>
                
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="phoneNumber">Contact Number</Label>            
                    <Input type="text" placeholder="Contact Number " id="phoneNumber" name="phoneNumber" value={this.state.formField.phoneNumber} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>
                    <FormGroup> 
                      <Label htmlFor="specializationId">Specialization</Label>            
                      <Input type="select" placeholder="Select Specialization" invalid={errors['specialization'] !== undefined && errors['specialization'] !== ""} id="specializationId" name="specializationId" value={""} onChange={this.changeSpecializationHandler} >
                        <option value="">Select Specialization</option>
                        {this.state.specializationList.map((categoryItem, index) =>
                          <SetDropDownItem key={index} optionsData={{id: categoryItem.specializationId, name: categoryItem.specializationName}}  />
                        )}
                      </Input>  
                      <div className="invalid-feedback">
                        {errors['specialization']}
                      </div>
                    </FormGroup> 
                    {
                      this.state.selectedSpecialization.length > 0 && <Col md = {"12"}>
                        <div className="addDrs">
                          <Row>
                            {
                              this.state.selectedSpecialization.map((specializationInfo, index) =>
                                <Col md= {6} key ={index}>
                                  <div className="select-user-info">
                                    {specializationInfo.specializationName}
                                    <button className="remove-selected-user-info" onClick = {(event) => {this.removeSelectedSpecialization(index, 'specialization', event)}}><i className="fa fa-times"></i></button>
                                  </div>
                                </Col> 
                              )
                            }
                          </Row>
                        </div>
                      </Col>
                    }
                </Col>
                <Col md={"6"}>
                  <FormGroup> 
                    <Label htmlFor="superSpecializationId">Super Specialization</Label>            
                    <Input type="select" placeholder="Select Super Specialization" invalid={errors['superSpecialization'] !== undefined && errors['superSpecialization'] !== ""} id="superSpecializationId" name="superSpecializationId" value={""} onChange={this.changeSpecializationHandler} >
                      <option value="">Select Super Specialization</option>
                      {this.state.superSpecializationList.map((categoryItem, index) =>
                        <SetDropDownItem key={index} optionsData={{id: categoryItem.specializationId, name: categoryItem.specializationName}}  />
                      )}
                    </Input>
                    <div className="invalid-feedback">
                      {errors['superSpecialization']}
                    </div>  
                  </FormGroup>  
                  {
                      this.state.selectedSuperSpecialization.length > 0 && <Col md = {"12"}>
                        <div className="addDrs">
                          <Row>
                            {
                              this.state.selectedSuperSpecialization.map((specializationInfo, index) =>
                                <Col md= {6} key ={index}>
                                  <div className="select-user-info">
                                    {specializationInfo.specializationName}
                                    <button className="remove-selected-user-info" onClick = {(event) => {this.removeSelectedSpecialization(index, 'superSpecialization', event)}}><i className="fa fa-times"></i></button>
                                  </div>
                                </Col> 
                              )
                            }
                          </Row>
                        </div>
                      </Col>
                    }
                </Col>
                <Col md={"6"}>
                    <FormGroup> 
                      <Label htmlFor="degreeId">Degree</Label>            
                      <Input type="select" placeholder="Select Degree" invalid={errors['degree'] !== undefined && errors['degree'] !== ""} id="degreeId" name="degreeId" value={this.state.formField.degreeId} onChange={this.changeHandler} >
                        <option value="">Select Degree</option>
                        {this.state.degreeList.map((categoryItem, index) =>
                          <SetDropDownItem key={index} optionsData={{id: categoryItem.degreeId, name: categoryItem.degreeName}}  />
                        )}
                      </Input>  
                      <div className="invalid-feedback">
                        {errors['degree']}
                      </div>
                    </FormGroup> 
                    
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="skype_id">Skype ID</Label>            
                    <Input type="text" placeholder="Skype ID " id="skype_id" name="skype_id" value={this.state.formField.skype_id} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="address">Address</Label> 
                    <Input type="text" placeholder="Address" id="address" name="address" value={this.state.formField.address} onChange={this.changeHandler}  />
                    {/*         
                    <AutoCompletePlaces setLatitudeLongitude={this.setLatitudeLongitude} address = {this.state.formField.address} />  */}
                  </FormGroup>
                </Col>                
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="postalCode">Postal Code</Label>            
                    <Input type="text" placeholder="Postal Code" id="postalCode" name="postalCode" value={this.state.formField.postalCode} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col lg={6}>
                    <FormGroup>
                      <Label htmlFor="template_status">Status</Label>            
                      <Input type="select" placeholder="Status *" id="status" name="status" value={this.state.formField.status} onChange={this.changeHandler} required >
                        <option value="">Select Status</option>
                        <option value="Active">Active</option>
                        <option value="Inactive">Inactive</option>
                        <option value="Pending">Pending</option>
                        <option value="Block">Block</option>
                      </Input>
                    </FormGroup>
                </Col>
              </Row>           
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Submit' }</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

function SetDropDownItem (props) {
  const optionsData = props.optionsData;
  return (<option value={optionsData.id} >{optionsData.name}</option>)
}

export default Doctors;
