import React, { Component } from 'react';

import  { Link } from 'react-router-dom';
import MUIDataTable from "mui-datatables";
import commonService from '../../../core/services/commonService';

class DoctorsData extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: [],
    };
    
  }
  componentDidMount() {   
  }
  /* Edit doctor Info */
  editDoctorItem(rowIndex){    
    this.props.editDoctorAction(rowIndex);
  }

  deleteDoctorItem(rowIndex){    
    this.props.deleteDoctorAction(rowIndex);
  }

  render() {
    
    let rowsItem = [];    
    for(const [i, orgnization] of this.props.data.entries()){
      console.log(i);
      let orgInfo = {
        doctorName: `${orgnization.firstName} ${orgnization.lastName}`,  
        firstName: `${orgnization.firstName} ${orgnization.lastName}`,
        email: orgnization.email,
        roleName: orgnization.doctorRole || " ",
        phoneNumber: orgnization.phoneNumber || " ",
        address: orgnization.address || " ",
        city: orgnization.city || " ",      
        state: orgnization.state || " ",
        country: orgnization.country || " ",
        skype_id: orgnization.skype_id || " ",
        status: commonService.getUserStatus(orgnization.status), //orgnization.status ? "Active" : "Inactive",   
        action: "",
        doctorAuthId: orgnization.authId,
        specialization: Array.isArray(orgnization.doctorsspecialization) ? orgnization.doctorsspecialization.map(item => item.specializationName).join(','): ""

      }      
      rowsItem.push(orgInfo);
    }      
    const columns = [      
      {
        label: 'Investigator Name',
        name: 'doctorName',
      },
      
      {
        label: 'Email',
        name: 'email',
      },

      {
        label: 'Phone Number',
        name: 'phoneNumber',
      },
      {
        label: 'Skype ID',
        name: 'skype_id',
      },

       {
        label: 'Address',
        name: 'address',
      },
      {
        label: 'Specialization',
        name: 'specialization',
      },
      {
        label: 'Status',
        name: 'status',
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            let i = tableMeta.rowIndex;
            let rowInfo = this.props.data[i];
            let authId = rowInfo.organizationId;
            return (
             <p><button title="Edit Doctor" className="btn-edit" disabled={this.state.buttonProcessing} onClick={() => 
          this.editDoctorItem(i)}><i className="fa fa-pencil"></i> </button>
          <Link title="View Doctor" className="btn-view" to={`/admin/manage-investigator/investigator/${authId}`}><i className="fa fa-user"></i> </Link>
          <button title="Delete Doctor" className="btn-delete" color="warning" disabled={this.state.buttonProcessing} onClick={() => {if( window.confirm('Are you sure you wish to delete this doctor?'))
          this.deleteDoctorItem(i)}}><i className="fa fa-trash"></i></button></p>
            );
          },
        }
      },
    ];
    const options = {
      search: false,viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: false,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      fixedHeaderOptions: { xAxis: false, yAxis: false }

    };
    
    
    return (
      <MUIDataTable
        
        data={rowsItem}
        columns={columns}
        options={options}
      />
    );
  }
}

export default DoctorsData;